import React from 'react';
import Spacer from '../components/Spacer';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import Tab from 'react-bootstrap/Tabs';
import Tabs from 'react-bootstrap/Tabs';

import {
  Switch,
  Route,
  Link,
  HashRouter,
} from "react-router-dom";
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Navbar'

import '../index.css';

import newlogo from '../assets/images/newlogo.png';

import foodii_back from '../assets/images/parallax_grid/foodii_background.png';
import foodii_front from '../assets/images/parallax_grid/foodii_foreground.png';
import planchat_back from '../assets/images/parallax_grid/planchat_background.png';
import planchat_front from '../assets/images/parallax_grid/planchat_foreground.png';
import gige_back from '../assets/images/parallax_grid/gige_background.png';
import gige_front from '../assets/images/parallax_grid/gige_foreground.png';

import robson_front from '../assets/images/parallax_grid/robson_foreground.png';
import robson_back from '../assets/images/parallax_grid/robson_background2.png';

import chair_front from '../assets/images/parallax_grid/chair_foreground.png';
import chair_back from '../assets/images/parallax_grid/chair_background.png';

import monument_front from '../assets/images/parallax_grid/monument_foreground2.png';
import monument_back from '../assets/images/parallax_grid/monument_background.png';

import { Parallax, ParallaxLayer, IParallax } from '@react-spring/parallax';



class ParallaxPage extends React.Component {

  constructor() {
    super();

    this.state = {
    };

    window.addEventListener("resize", this.update);
  }

  componentDidMount() {
    this.update();
  }

  update = () => {
    setTimeout(() => {
      this.setState({
      height: window.innerHeight,
      dynamicpages: (2200 / window.innerHeight) + (300 / window.innerWidth),
    });
  }, 20)
  };



render() {
  return (
    <div className='ParallaxDiv'>


    <Parallax pages={this.state.dynamicpages} style={{ top: '0', left: '0'}} className="HomeParallax">

      <ParallaxLayer offset={0} speed={0} style={{zIndex: '1'}} factor={0.01}>

      <Navbar className="homenav" expand="lg" variant="dark" >
        <Link className="navlogo" to="/"><img src={newlogo} alt="sitelogo" width="30"/></Link>
        <Navbar.Toggle className="border-0" aria-controls="navbar.toggle"  className="collapsebar"/>
        <Navbar.Collapse id="navbar-toggle" >
          <Nav className="ml-auto">

          <Link className="homenav-link" to="/">Work</Link>
          <Link className="homenav-link" to="/aboutpage">About</Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
  <Spacer/><Spacer/><Spacer/>
      </ParallaxLayer>
      <ParallaxLayer
        offset={0}
        speed={0.7}
        style={{ display: 'flex', justifyContent: 'center',
        alignItems: 'center', backgroundColor: '#000d1a' , height: '50vh'}}
        className="HomeHero">

      </ParallaxLayer>

      <ParallaxLayer offset={0} speed={0.4} style={{ display: 'flex', justifyContent: 'center',
      alignItems: 'center', height: '50vh'}} >
      <Container>
      <Row>
        <Col lg={6} className="homeheroleft" style={{textAlign: 'right'}}>
          <h5 className="nametext">Aaron Lo</h5><br/><br/><br/><br/>
          <h5 className="homeblurb">Designing UX solutions that deliver </h5>
          <h5 className="homeblurbsarif">delightful experiences.</h5>
        </Col>
        <Col lg={6} className="homeheroright">
          <h5 className="uxtext">UX <br/>Designer</h5>
        </Col>
      </Row>
      </Container>
      </ParallaxLayer>

      <ParallaxLayer offset={0} style={{ display: '', justifyContent: '',
      alignItems: '', marginTop: '47vh', marginLeft: ''}} factor={0.5}>
      <Container>
      <Tabs defaultActiveKey="DigitalWork" className="">

        <Tab eventKey="DigitalWork" title="Digital Work">
        <Spacer/><Spacer/><Spacer/>
          <Container>
            <Row>
              <Col lg={6} className="projectleft" >
              <ParallaxLayer speed={1} offset={0} speed={0}>
                <Link className="parallaxback" to="/foodpage"><img src={foodii_back} className="parallaxbackimg" alt="" width="400px"/></Link>
              </ParallaxLayer>
              <ParallaxLayer speed={1} offset={0} speed={0.02}>
                <Link className="parallaxfront" to="/foodpage"><img src={foodii_front} className="parallaxfrontimg" alt="" width="450px"/></Link>
              </ParallaxLayer>
              </Col>
              <Col  lg={6} className="projectright">
              <br/><br/><br/>
              <h5 className="projectname">Foodii: A Tabbed Navigation Solution for Comparing Restaurants</h5>
              <h6 className="projectblurb">Designing navigational architecture to support an iterative process
              of searching, browsing, and comparing.</h6>
              <br/>
              <Link className="projectbutton" to="/foodpage">View Case Study</Link>
              </Col>
            </Row>
          </Container>
          <div className="variablespacer"/>
          <Container>
            <Row>
              <Col lg={6} className="projectleft" >
              <ParallaxLayer speed={1} offset={0} speed={0} horizontal={true}>
                <Link className="parallaxback" to="/planchatpage"><img src={planchat_back} className="parallaxbackimg" alt="" width="400px"/></Link>
              </ParallaxLayer>
              <ParallaxLayer speed={1} offset={0} speed={0.04}>
                <Link className="parallaxfront" to="/planchatpage"><img src={planchat_front} className="parallaxfrontimg"
                alt="" width="300px" style={{ marginTop: '30%', marginLeft: '30%'}}/></Link>
              </ParallaxLayer>
              </Col>
              <Col  lg={6} className="projectright">
              <br/><br/><br/>
              <h5 className="projectname">PlanChat: Gap Analysis on Hangout-Planning Tools</h5>
              <h6 className="projectblurb">Conducting gap analysis on Facebook Events and Discord and designing a hangout-planning solution
              to reduce user confusion.</h6>
              <br/>
              <Link className="projectbutton" to="/planchatpage">View Case Study</Link>
              </Col>
            </Row>
          </Container>
          <div className="variablespacer"/>
          <Container>
            <Row>
              <Col lg={6} className="projectleft" >
              <ParallaxLayer speed={1} offset={0} speed={0}>
                <Link className="parallaxback" to="/project1page"><img src={gige_back} alt="" className="parallaxbackimg" width="400px"/></Link>
              </ParallaxLayer>
              <ParallaxLayer speed={1} offset={0} speed={0.03}>
                <Link className="parallaxfront" to="/project1page"><img src={gige_front} alt="" className="parallaxfrontimg" width="350px" style={{ marginTop: '40%', marginLeft: '20%'}}/></Link>
              </ParallaxLayer>
              </Col>
              <Col  lg={6} className="projectright">
              <br/><br/><br/>
              <h5 className="projectname">GIGE</h5>
              <h6 className="projectblurb">Currently designing digital ecosystems at GIGE Corporation</h6>
              <br/>
              <Link className="projectbutton" to="/project1page">View Case Study</Link>
              </Col>
            </Row>
          </Container>
        </Tab>

        <Tab eventKey="PhysicalWork" title="Physical Work">
        <Spacer/><Spacer/><Spacer/>
          <Container>
            <Row>
              <Col lg={6} className="projectleft" >
              <ParallaxLayer speed={1} offset={0} speed={0}>
                <Link className="parallaxback" to="/bandpage"><img src={chair_back} alt="" className="parallaxbackimg" width="400px"/></Link>
              </ParallaxLayer>
              <ParallaxLayer speed={1} offset={0} speed={0.02} style={{ marginTop: '7%', marginLeft: '20%'}}>
                <Link className="physparallaxfront" to="/bandpage"><img src={chair_front} className="parallaxfrontimg" alt="" width="250px"/></Link>
              </ParallaxLayer>
              </Col>
              <Col  lg={6} className="projectright">
              <br/><br/><br/>
              <h5 className="projectname">Guitarist Chair: Prototyping an Ergonomic Seating Solution</h5>
              <h6 className="projectblurb">Rapid prototyping and building a stool to address the needs of performing
              guitarists.</h6>
              <br/>
              <Link className="projectbutton" to="/bandpage">View Case Study</Link>
              </Col>
            </Row>
          </Container>
          <div className="variablespacer"/>
          <Container>
            <Row>
              <Col lg={6} className="projectleft" >
              <ParallaxLayer speed={1} offset={0} speed={0} horizontal={true}>
                <Link className="parallaxback" to="/monumentpage"><img src={monument_back} alt="" className="parallaxbackimg" width="400px"/></Link>
              </ParallaxLayer>
              <ParallaxLayer speed={1} offset={0} speed={0.03}>
                <Link className="physparallaxfront" to="/monumentpage"><img src={monument_front} className="parallaxfrontimg"  alt="" width="320px" style={{ marginTop: '20%', marginLeft: '30%'}}/></Link>
              </ParallaxLayer>
              </Col>
              <Col  lg={6} className="projectright">
              <br/><br/><br/>
              <h5 className="projectname">Monument: A Study on Design Systems</h5>
              <h6 className="projectblurb">Designing the relationships between components,
              arrangement guidelines, and the hollistic whole.</h6>
              <br/>
              <Link className="projectbutton" to="/monumentpage">View Case Study</Link>
              </Col>
            </Row>
          </Container>
          <div className="variablespacer"/>
          <Container>
            <Row>
              <Col lg={6} className="projectleft" >
              <ParallaxLayer speed={1} offset={0} speed={0}>
                <Link className="parallaxback" to="/robsonpage"><img src={robson_back} className="parallaxbackimg" alt="" width="400px"/></Link>
              </ParallaxLayer>
              <ParallaxLayer speed={1} offset={0} speed={0.01}>
                <Link className="physparallaxfront" to="/robsonpage"><img src={robson_front} className="parallaxfrontimg" alt="" width="500px" style={{ marginTop: '25%', marginLeft: ''}}/></Link>
              </ParallaxLayer>
              </Col>
              <Col  lg={6} className="projectright">
              <br/><br/><br/>
              <h5 className="projectname">Robson Square: An Architectural Heuristic Evaluation</h5>
              <h6 className="projectblurb">Conducting an analysis on present
              and future issues facing a public square in Vancouver</h6>
              <br/>
              <Link className="projectbutton" to="/robsonpage">View Case Study</Link>
              </Col>
            </Row>
          </Container>
        </Tab>
        </Tabs>
      </Container>
      </ParallaxLayer>
    </Parallax>
    </div>
  );
}
}

export default ParallaxPage;
