import React from 'react';
import Psummary from '../components/Psummary';
import HeroLeftRight from '../components/HeroLeftRight';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import HeroImage from '../components/HeroImage';
import MenuIos from '../components/MenuIos';
import ScrollTop from '../components/ScrollTop';
import CenteredTitle from '../components/CenteredTitle';
import BackButton from '../components/BackButton';
import Container from 'react-bootstrap/Container';
import Divider from '../components/Divider';

import {
  Switch,
  Route,
  Link,
  HashRouter,
} from "react-router-dom";
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Navbar'

import lologo from '../assets/images/lo_logo.png';


function AboutPage(props) {

  return (
    <div>
    <Navbar  fixed="top" className="shadownav" expand="lg" variant="light">
      <Link className="navlogo" to="/"><img src={lologo} alt="sitelogo" width="30"/></Link>
      <Navbar.Toggle className="border-0" aria-controls="navbar.toggle"  className="collapsebar"/>
      <Navbar.Collapse id="navbar-toggle" >
        <Nav className="ml-auto">

        <Link className="pagenav-link" to="/">Work</Link>
        <Link className="pagenav-link" to="/aboutpage">About</Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
    <div  className="divmovedown">
    <BackButton/>
    <Container>

    <div class="titletext">
    <Spacer />

    <Spacer/>
    <div className="blurbdiv">
    <p className="darknametext">Aaron Lo</p><Spacer />
    <p className="">Drawing from a multidisciplinary background in UX, marketing, and architecture,
    Aaron brings a unique design perspective to any project. Coupled with his iterative
    process of rigorous user research and interaction design, he produces
    products that deliver delightful user experiences and concrete business value.</p>
    </div>
    </div>


    </Container>

    <ScrollTop />
    </div>
    </div>
  );
}

export default AboutPage
